body {
    font-family: 'Josefin Sans', sans-serif;
    padding-bottom: 20px;
  }
  
.backgroundimg {
    background-size: cover;
    background-repeat: repeat-x;
    background-position: center;  
    height: 200%;
    width: 100%;
    overflow: visible; 
    margin: 0%;
    vertical-align:bottom;
}

.moonsun {
    text-align: right;
    color:#06f0f0;
}

 
#footer {
    color:rgba(8, 170, 245, 1);
      font-family: 'Josefin Sans', sans-serif;
  }
 

  #about {
    text-align: left;
    padding-top: 7px;
    padding-left: auto;
    margin-right: auto;
  }
  
  .headshot {
   
      width: auto;
      height: auto;
      max-width: 90%;
      max-height: 90%;
  }
  
  .headline {
    text-align: center;
    font-family: 'Josefin Sans', sans-serif;
  }

  .new-line {
    white-space: pre-line;
  }


.wrapper {
    margin-left: 10vh;
    margin-right: 10vh;
    margin-top: 20vh;
    margin-bottom: 20vh;
  }
  
  .container {
    border: 20px black;
    background-color: rgba(8, 170, 245, 0.7);
    padding: 20px 20px;
    align-self: center;
    color: black;
    flex-wrap: wrap;
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
  backdrop-filter: blur( 5.0px );
  -webkit-backdrop-filter: blur( 11.0px );
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );
